import React, { useState } from 'react'
import {
  ComposableMap,
  Geographies,
  Geography
} from 'react-simple-maps'
import ReactTooltip from 'react-tooltip'

import { scaleQuantize } from 'd3-scale'

import data from './data'

const geoUrl = 'https://raw.githubusercontent.com/zcreativelabs/react-simple-maps/master/topojson-maps/world-110m.json'

const confirmedScale = scaleQuantize().domain([1, 10]).range(['#edeaff', '#cec5ff', '#ad9fff', '#8a75ff', '#5533ff', '#492de2', '#3d26be', '#311f9a', '#261878'])
const currencyScale = scaleQuantize().domain([0, 10]).range(['#ffedea', '#ffcec5', '#ffad9f', '#ff8a75', '#ff5533', '#e2492d', '#be3d26', '#9a311f', '#782618'])

function hexToRgb (hex) {
  var result = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(hex)
  return result ? [parseInt(result[1], 16), parseInt(result[2], 16), parseInt(result[3], 16)] : null
}

function rgbToHex (r, g, b) {
  return '#' + ((1 << 24) + (r << 16) + (g << 8) + b).toString(16).slice(1)
}

// colorChannelA and colorChannelB are ints ranging from 0 to 255
function colorChannelMixer (colorChannelA, colorChannelB, amountToMix) {
  var channelA = colorChannelA * amountToMix
  var channelB = colorChannelB * (1 - amountToMix)
  return parseInt(channelA + channelB)
}
// rgbA and rgbB are arrays, amountToMix ranges from 0.0 to 1.0
// example (red): rgbA = [255,0,0]
function colorMixer (rgbA, rgbB, amountToMix) {
  var r = colorChannelMixer(rgbA[0], rgbB[0], amountToMix)
  var g = colorChannelMixer(rgbA[1], rgbB[1], amountToMix)
  var b = colorChannelMixer(rgbA[2], rgbB[2], amountToMix)
  return rgbToHex(r, g, b)
}

const MapChart = ({ date }) => {
  const [tooltipContent, setTooltipContent] = useState('')
  const [selectedCountry, selectCountry] = useState('')

  return (
    <>
      <ComposableMap data-tip='' projectionConfig={{ scale: 200 }} projection='geoNaturalEarth1'>
        <Geographies geography={geoUrl}>
          {({ geographies }) =>
            geographies.map(geo => {
              const initial = data['2020-01-22'].find(s => s.country === geo.properties.ISO_A2)
              const cur = data[date].find(s => s.country === geo.properties.ISO_A2)

              let content

              let changes
              if (cur && cur.currency && cur.rate) {
                changes = Math.round(10000.0 * (initial.rate - cur.rate) / initial.rate) / 100.0
                let changesString

                if (changes > 0) {
                  changesString = ` ${changes}%`
                } else if (changes < 0) {
                  changesString = ` ${changes}%`
                } else {
                  changesString = `  ${changes}%`
                }

                content = `${geo.properties.NAME} | ${cur.confirmed} confirmed | currency ${changesString}`

                if (cur.country === selectedCountry) {
                  setTooltipContent(content)
                }
              } else if (cur && cur.confirmed) {
                content = `${geo.properties.NAME} | ${cur.confirmed} confirmed`
              } else {
                content = `${geo.properties.NAME}`
              }

              const confirmed = cur ? confirmedScale((10.0 * (Math.log10(cur.confirmed) / 5.30102999566))) : '#eeeeee'
              const losses = changes ? currencyScale(10.0 * (changes / -26.0)) : '#eeeeee'
              const combined = colorMixer(hexToRgb(confirmed), hexToRgb(losses), 0.25)

              const fill = combined

              return (
                <Geography
                  key={geo.rsmKey}
                  geography={geo}
                  onMouseEnter={() => {
                    selectCountry(geo.properties.ISO_A2)
                    setTooltipContent(content)
                  }}
                  onMouseLeave={() => {
                    setTooltipContent('')
                    selectCountry('')
                  }}
                  fill={fill}
                  style={{
                    default: { outline: 'none' },
                    hover: { outline: 'none' },
                    pressed: { outline: 'none' }
                  }}
                />
              )
            })}
        </Geographies>
      </ComposableMap>
      <ReactTooltip>{tooltipContent}</ReactTooltip>
    </>
  )
}

export default MapChart
