import React, { useState, useEffect } from 'react'

import './bootstrap.min.css'

import Container from 'react-bootstrap/Container'
import Form from 'react-bootstrap/Form'
import Col from 'react-bootstrap/Col'
import Row from 'react-bootstrap/Row'
import Navbar from 'react-bootstrap/Navbar'
import Nav from 'react-bootstrap/Nav'
import Button from 'react-bootstrap/Button'

import moment from 'moment'

import MapChart from './MapChart'

import './App.css'

const theDate = ['2020-01-22', '2020-01-23', '2020-01-24', '2020-01-27', '2020-01-28', '2020-01-29', '2020-01-30', '2020-01-31', '2020-02-03', '2020-02-04', '2020-02-05', '2020-02-06', '2020-02-07', '2020-02-10', '2020-02-11', '2020-02-12', '2020-02-13', '2020-02-14', '2020-02-17', '2020-02-18', '2020-02-19', '2020-02-20', '2020-02-21', '2020-02-24', '2020-02-25', '2020-02-26', '2020-02-27', '2020-02-28', '2020-03-02', '2020-03-03', '2020-03-04', '2020-03-05', '2020-03-06', '2020-03-09', '2020-03-10', '2020-03-11', '2020-03-12', '2020-03-13', '2020-03-16', '2020-03-17', '2020-03-18', '2020-03-19', '2020-03-20', '2020-03-23', '2020-03-24', '2020-03-25', '2020-03-26', '2020-03-27', '2020-03-30', '2020-03-31', '2020-04-01']

const App = () => {
  const { innerHeight } = window
  const [date, setDate] = useState(theDate[0])
  const [counter, setCounter] = useState(0)
  const [isRunning, setIsRunning] = useState(true)

  const handleTimer = () => {
    let duration
    if (counter < theDate.length - 1) duration = 1000
    else duration = 5000

    setTimeout(() => {
      if (counter < theDate.length - 1) setCounter(counter + 1)
      else setCounter(0)

      setDate(theDate[counter])
    }, duration)
  }

  useEffect(() => {
    console.log(isRunning)
    if (isRunning) handleTimer()
  })

  useEffect(() => {
    window.scrollTo({
      behavior: 'smooth',
      top: innerHeight
    })
  }, [])

  return (
    <>
      <Navbar expand='lg'>
        <Nav inline='true' className='mr-auto'>
          <Navbar.Brand><a href='https://github.com/linerocks'>linerocks</a></Navbar.Brand>
        </Nav>
      </Navbar>
      <Container className='mt-3 mb-3'>
        <h1 className='header' style={{ fontSize: '44pt' }}>Currency vs Coronavirus</h1>
        <h4 className='header mb-3'>Global Currency Exchange Outlook During COVID-19 Period</h4>

        <Row>
          <Col className='col-12'>
            <div style={{ fontSize: '14pt', width: '85%', textAlign: 'center', margin: 'auto' }}>
              <MapChart date={date} isCurrency />
            </div>
          </Col>
        </Row>
        <Row>
          <Col>
            {
              isRunning
                ? <><Button variant='warning' size='sm' onClick={() => { setIsRunning(false) }}>Pause</Button></>
                : <><Button variant='primary' size='sm' onClick={() => { setIsRunning(true) }}>Resume</Button></>
            }

          </Col>
          <Col>
            <span style={{ color: '#8a75ff' }}></span><span> confirmed coronavirus case</span>
          </Col>
          <Col>
            <span style={{ color: '#ff8a75' }}></span><span> currency loss</span>
          </Col>
          <Col className='align-middle text-right'>
            <p>{moment(date).format('DD MMMM YYYY')}</p>
          </Col>
        </Row>
      </Container>
    </>
  )
}

export default App
